@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{margin:0;padding:0;border:0;line-height:1.4em;vertical-align:baseline;text-decoration:none;outline:0;font-family:"Roboto Mono", monospace}section{display:flex;background-color:black;height:10vh;flex-direction:row;justify-content:space-evenly}article{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;font-family:"Lora", serif;align-items:center}section :nth-child(1){border:0.25px solid green;color:white}section :nth-child(2){border:0.25px solid blue;color:white}.center{height:10vh}.topnav{display:flex;width:auto;background-color:black;padding-bottom:25px;color:white;justify-content:left;height:8vh}.topnav a{margin-top:5px;display:inline-block;text-decoration:none;text-align:justify;padding:6px 12px;color:white;font-size:17px;font-family:"Roboto Mono", monospace}.topnav a:hover{color:#6699ff}#mainBackground{background-image:url(/simple-app/static/media/_05A5998.5180c15f.jpg);background-size:cover;height:65vh;width:auto;position:relative;max-width:100%}#mainBackground h1{font-size:100px;margin-top:190px;color:white;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center}#mainBackground h2{font-size:15px;text-align:center;color:white;margin-top:0px}ul{padding:0;justify-items:center}footer ul{list-style:none;width:100%;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}footer>ul>li{display:inline-block;position:relative}footer{background-color:black;text-decoration:none;font-size:17px;height:20vh;display:flex;color:white}footer ul li:hover{color:#6699ff;background-color:black;cursor:pointer}@media screen and (max-width: 918px){#mainBackground h1{font-size:30px}section{display:none}}@media screen and (max-width: 500px){#mainBackground h2{font-size:10px}#mainBackground h1{font-size:15px}}@media screen and (max-width: 391px){#mainBackground h2{display:none}#mainBackground h1{display:none}}

